<template>
    <div class="swipe-in">
    <MainBanner />

    <div class="container">
        <article>
            <time>{{ data }}</time>
            <header>
                <h1>Mega-Sena {{ concurso }} acumula e prêmio pode chegar a {{ acumuladaProxConcurso }}; veja dezenas</h1>
                <h2 v-for="(acerto, index) in premiacoes" :key="index">
                    <span v-if="(acerto.acertos === 'Quina')">A Caixa informou que {{ acerto.vencedores }} apostadores acertaram a {{ acerto.acertos }}</span>
                </h2>
            </header>
            <div id="bodyArticle">
                <p>
                    Ninguém acertou as seis dezenas do concurso {{ concurso }} da Mega-Sena realizado no dia {{ data }}. Os números sorteados pela Caixa foram: 
                </p>
                <div class="flex">
                    <span v-for="(dezena, index) in dezenas" :key="index" class="dz">
                        {{ dezena }}
                    </span>
                </div>
            </div>
            <p>
                O sorteio foi realizado no Espaço da Sorte, em São Paulo (SP). O próximo concurso será no dia {{ dataProxConcurso }}, e o prêmio estimado é de {{ acumuladaProxConcurso }}.
            </p>
            <p v-for="(acerto, index) in premiacoes" :key="index">
                <span v-if="(acerto.acertos === 'Quina')">
                    A Caixa informou ainda que {{ acerto.vencedores }} apostadores acertaram a {{ acerto.acertos }} (cinco dezenas), levando R$ {{ acerto.premio }}.
                </span>
            </p>
            <p v-for="(acerto, index) in premiacoes" :key="index">
                <span v-if="(acerto.acertos === 'Quadra')">
                    Outras {{ acerto.vencedores }} apostas fizeram a quadra (quatro dezenas), ganhando um prêmio de R$ {{ acerto.premio }} cada uma.
                </span>
            </p>
            <footer>
                <p>
                    E por falar em <b>Mega-Sena</b>, a loteria mais popular no Brasil poderá pagar <b>{{ acumuladaProxConcurso }}</b> para quem cravar as seis dezenas no próximo sorteio do concurso <b>{{ concurso }}</b> 
                    que acontecerá no dia {{ dataProxConcurso }} às 20h (horário de Brasília).
                </p>
                <p>
                    No mais antigo site de jogos lotéricos do Brasil, o <b><a href="https://investloto.com.br" target="_blank">Investloto</a></b>, você consegue apostar na Mega com a maior facilidade e rapidez, além de aumentar suas chances 
                    com os bolões.
                </p>
                <router-link to="/" class="prev-btn">Página inicial</router-link>
            </footer>
        </article>
    </div>
    </div>
</template>

<script>
const today = new Date()

import axios from 'axios'
import MainBanner from '@/components/MainBanner.vue'

export default {
    name: 'AllAricles',
    components: { MainBanner },
    data() {
        return {
            day: today.getDate(),
            month: today.getMonth(),
            year: today.getFullYear(),
            concurso: null,
            data: null,
            dezenas: null,
            premiacoes: null,
            acumulou: null,
            acumuladaProxConcurso: null,
            dataProxConcurso: null,
        }
    },
    mounted() {
        axios.get('https://loteriascaixa-api.herokuapp.com/api/mega-sena/latest')
        .then(response => (this.concurso = response.data.concurso, this.data = response.data.data, this.dezenas = response.data.dezenas, this.premiacoes = response.data.premiacoes, 
        this.acumulou = response.data.acumulou, this.acumuladaProxConcurso = response.data.acumuladaProxConcurso, this.dataProxConcurso = response.data.dataProxConcurso))
    }
}
</script>

